import xflSelect from '@/components/xfl-select/xfl-select.vue';
import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue';
import wPicker from '@/components/w-picker/w-picker.vue';
import mPicker from '@/components/w-picker/sb-picker.vue';
export default {
	components: {
		xflSelect,
		mpvueCityPicker,
		wPicker,
		mPicker
	},
	data() {
		return {
			showPicker: false,
			endYear: new Date(),
			active: 0,
			dataObject: {
				userIcon: '', //用户头像、
				nickName: '', //用户昵称
				phoneNum: '', //用户电话
				money: '', //期望薪资
				birthDay: '', //出生年月日
				sex:'', //性别
				leaveaddress: '', //当前居住地
				leaveaddressDetails: '', //当前居住地详细地址
				expectedWorkAddress: '', //期望工作地
				educational: '', // 最高学历
				worksGroupList: '', // 曾就业过的企业
				worksYear: '', // 工龄
				siklledBook: '', //职称技能证书
				bounsSinge: '', //我的获奖
				oneSinge: '', //一句话优点
				showPhone: 1, // 是否显示电话 0 否  1 是
				showName: 1, //是否显示姓名 0 否  1 是
				showAddress: 1, //是否显示地址 0 否  1 是
				findWorkClass: 1, //  找工作类型
				expectedWork:'',
				worksList:''
			},
			expectedWork:[],
			sillkedClassList: [{
					value: '全职'
				},
				{
					value: '兼职'
				},
				{
					value: '学生工'
				}
			],
			sexList: [{
					value: '男'
				},
				{
					value: '女'
				}
			],
			educationalList: [{
					value: '小学'
				},
				{
					value: '初中'
				},
				{
					value: '高中'
				},
				{
					value: '专科'
				},
				{
					value: '本科'
				}
			]
		};
	},
	onLoad() {
		this.loadData();
	},
	watch:{
		expectedWork(newVal){
			if(newVal){
				this.$forceUpdate();
				let arry=[];
				newVal.forEach(item=>{
					arry.push(item.name)
				})
				this.dataObject.expectedWork=arry.join(',')
			}
			console.log(this.dataObject.expectedWork)
		}
	},
	methods: {
		loadData() {
			let parmas = {
				uid: getApp().globalData.uid
			};
			this._reqw
				.ipost(parmas, 'mybiographical')
				.then(res => {
					res.result == 0 ? this.dataObject = res : this.$api.tip(res.resultNote);
				})
				.catch(err => []);
		},
		changeWorkClass(e){
			console.log(e)
			this.dataObject.findWorkClass=this.sillkedClassList[e].value;
		},
		changeEd(e) {
			console.log(e)
			this.dataObject.educational =this.educationalList[e].value;
		},
		onMoneyConfirm(e) {
			console.log(e);
			this.dataObject.money = e.join('-')+'k';
		},
		choseStar() {
			this.$refs.date.show();
		},
		choseWorksMoney() {
			this.$refs.mPicker.open();
		},
		choseAddress(k) {
			this.active = k;
			this.$refs.mpvueCityPicker.show();
		},
		addressConfirm(e) {
			console.log(e)
			this.active == 0 ? this.dataObject.leaveaddress = e.label : this.dataObject.expectedWorkAddress = e.label
		},
		onConfirm(e) {
			// console.log(e);
			this.dataObject.birthDay = e.result;
		},
		JobIntention() {
			this.$api.navTo('/pages/choseClass');
		},
		changeSex(e) {
			console.log(e)
			this.dataObject.sex = e
		},
		meritDesc(type) {
			if (type == 1) {
				this.$api.navTo('/pages/befault');
			} else if (type == 0) {
				this.$api.navTo('/pages/userName');
			}
		},
		subInfo() {
			let dataObject = this.dataObject;
			console.log(dataObject.findWorkClass)
			// if (dataObject.userIcon == '') {
			// 	this.$api.tip('用户头像不能为空');
			// } else 
			if (dataObject.nickName == '') {
				this.$api.tip('用户头像不能为空');
			} else if (dataObject.phoneNum == '') {
				this.$api.tip('手机号码不能为空');
			} else if (dataObject.birthDay == '') {
				this.$api.tip('出生日期不能为空');
			} else if (dataObject.sex==='') {
				this.$api.tip('请选择性别');
			} else if (dataObject.leaveaddress == '') {
				this.$api.tip('当前居住地不能为空');
			} else if (dataObject.expectedWorkAddress == '') {
				this.$api.tip('期望工作地不能为空');
			} else if (dataObject.expectedWork =='') {
				this.$api.tip('请选择期望工作');
			} 
			else if (dataObject.findWorkClass ==='') {
				this.$api.tip('请选择找工作类型');
			}else {
				dataObject.lat="134.77";
				dataObject.lng="34.77";
				let parmas = {
					uid: getApp().globalData.uid,
					nickName: dataObject.nickName,
					phoneNum: dataObject.phoneNum,
					sex: dataObject.sex,
					birthDay: dataObject.birthDay,
					leaveaddress: dataObject.leaveaddress,
					leaveaddressDetails: dataObject.leaveaddressDetails,
					lat: dataObject.lat,
					lng: dataObject.lng,
					findWorksClass: dataObject.findWorkClass,
					expectedWork: dataObject.expectedWork,
					money: dataObject.money,
					worksaddress: dataObject.expectedWorkAddress,
					worksYear: dataObject.worksYear,
					siklledBook: dataObject.siklledBook,
					bounsSinge: dataObject.bounsSinge,
					oneSinge: dataObject.oneSinge,
					educational: dataObject.educational,
					worksList: dataObject.worksList,
					worksGroupList: dataObject.worksGroupList
				};
				this._reqw
					.ipost(parmas, 'subBiographical')
					.then(res => {
						res.result == 0 ?
							(this.$api.tip('提交成功!'),
								setTimeout(() => {
									this.$api.back();
								}),
								300) :
							this.$api.tip(res.resultNote);
					})
					.catch(err => {});
			}
		}
	}
};
